import { gql } from '@apollo/client';

export const EXCHANGE_RATES = gql`
  query GetExchangeRates {
    rates(currency: "USD") {
      currency
      rate
    }
  }
`

export const GET_POKEMONS = gql`
    query pokemons {
        pokemons(limit: 30) {
        count
        next
        previous
        status
        message
        results {
            url
            name
            image
        }
        }
    }
`